import { BigNumber, utils } from 'ethers'

function getSubAccount(owner: string, id: number) {
  return utils.getAddress(BigNumber.from(owner).xor(id).toHexString())
}

export function getEulerSubAccounts(owner: string) {
  return Array.from({ length: 256 }, (_, id) => {
    const subAccount = getSubAccount(owner, id)
    return subAccount
  })
}
